.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
}

.header__logo {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  padding: 0 16px;
  transition-duration: .3s;
  display: flex;
}

.card__title {
  text-align: left;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__picture-imac {
  width: 160px;
  height: 134px;
  margin: 32px auto 40px;
  display: flex;
}

.card__product-code-imac {
  color: #616070;
  text-align: left;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rewiev {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 24px;
  display: flex;
}

.card__text-amount-review {
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-product {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__text-price {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__product-price {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:not(:last-child):first-child, .stars--2 .stars__star:not(:last-child):nth-child(-n+2), .stars--3 .stars__star:not(:last-child):nth-child(-n+3), .stars--4 .stars__star:not(:last-child):nth-child(-n+4), .stars--5 .stars__star:not(:last-child):nth-child(n) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body, * {
  box-sizing: border-box;
  margin: 0;
}
